$textBoxPadding: 10px 20px;
$maxWidth: 500px;
$commonBorder: solid 2px #000;

body {
  background-color: #f1f1f1;
  // background-image: url("data:image/svg+xml,%3Csvg width='52' height='26' viewBox='0 0 52 26' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23000000' fill-opacity='0.1'%3E%3Cpath d='M10 10c0-2.21-1.79-4-4-4-3.314 0-6-2.686-6-6h2c0 2.21 1.79 4 4 4 3.314 0 6 2.686 6 6 0 2.21 1.79 4 4 4 3.314 0 6 2.686 6 6 0 2.21 1.79 4 4 4v2c-3.314 0-6-2.686-6-6 0-2.21-1.79-4-4-4-3.314 0-6-2.686-6-6zm25.464-1.95l8.486 8.486-1.414 1.414-8.486-8.486 1.414-1.414z' /%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  background-image: url("../image/bg1.png");
  font-size: 20px;
  margin: 0;
  font-family: monospace;

  & input {
    font-size: 20px;
  }
}

.charactar-place-holder {
  display: inline-block;
  background-color: #0003;
  height: 1em;
}

.charactar-cube {
  // display: inline-block;
  // height: 1.4em;
  // line-height: 1.4em;
  // width: 1.4em;
  // border: 1px solid #0003;
  text-align: center;
  user-select:none;
}

#dev-info {
  font-size: 0.4em;
  line-height: 2em;
  color: #f00;
  position: fixed;
  top: 300px;
  margin: 0 0.5em;
}

.target-setting {
  font-family: monospace;
  max-width: $maxWidth;
  font-size: 20px;

  textarea {
    box-sizing: border-box;
    font-size: 20px;
    margin: 6px 0 10px;
    height: 160px;
    width: 100%;
    resize: none;
    outline-style: none;
    padding: 0 16px;
    // padding: $textBoxPadding;
    border: none;
    color: #000;
    background: transparent;
    &::placeholder {
      color: #0002;
    }
  }


  .part-title {
    user-select: none;
    padding: 6px 14px 0;
    font-size: 16px;
    text-align: left;
    margin-top: 10px;
  }

  .part-info {
    user-select: none;
    padding: 0 14px 0;
    font-size: 8px;
    color: #0005;
    text-align: left;
    margin-top: 2px;
  }

  .button-list {
    display: flex;
    flex-wrap: wrap;
    padding: 10px 0px 0px 10px;
    
    button {
      background-color: #0002;
      border-radius: 1000000px;
      border: solid 1px #0002;
      color: #000a;
      font-size: 0.5em;
      font-weight: 500;
      height: 26px;
      line-height: 26px;
      margin: 0 12px 11px 0;
      padding: 0 10px;
      width: auto;
      &:hover {
        background-color: #0004;
        cursor: pointer;
      }
    }
    
    .secondary-btn {
      background-color: #1172;
      &:hover {
        background-color: #1174;
      }
    }
  }
}

.statistics {
  padding: $textBoxPadding;
  font-size: 0.5em;
  max-width: $maxWidth;
  font-family: monospace;
  text-align: left;

  .slowZi {
    display: flex;
    flex-wrap: wrap;
    & > div {
      width: 7.5em;
    }
  }
}

